import React, { Suspense } from 'react';

interface EditorModalProps {
    children: React.ReactNode;
    onClose: () => void;
}

export const EditorModal: React.FC<EditorModalProps> = ({ children, onClose }) => {
    return (
        <div 
            style={{
                position: 'fixed',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                backgroundColor: 'rgba(0, 0, 0, 0.5)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                zIndex: 1000,
            }}
            onClick={onClose}
        >
            <div 
                style={{
                    backgroundColor: 'white',
                    padding: '24px',
                    borderRadius: '8px',
                    maxWidth: '600px',
                    width: '90%',
                    maxHeight: '80vh',
                    overflow: 'auto',
                    boxShadow: '0 4px 20px rgba(0, 0, 0, 0.15)',
                }}
                onClick={(e) => e.stopPropagation()}
            >
                <Suspense fallback={<div>Loading editor...</div>}>
                    {children}
                </Suspense>
            </div>
        </div>
    );
};
